@import "../inc/config";
.screen_locations {
  flex-direction: column;
  li.location {
    display: flex;
    align-items: center;
    padding: 10px 5px;
    margin: 5px;
    background: $color1;
    min-height: 65px;


    .num {
      width: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-transform: uppercase;
      font: 400 16px/16px $font;
      color: $color2;
    }

    .info {
      display: flex;
      flex: 1;
    }
  }
}
